import React, { Component} from 'react';
//import POC from './Component/POC.css'

class Home extends Component {
    state ={
        username : '',
        password : '',
        error : false
    }
    authCheckHandler = () => {
        console.log(this.state.username, this.state.password)
        if(this.state.username==="Admin" & this.state.password==="Admin"){
            this.setState({error : false})
            this.props.history.push('/fetchData');
        }
        else{
            this.setState({error : true})
        }
    }
    render(){
        return(
            
            <div>
                <center>
                <p><b>Username:  </b></p>
                <input 
                    type="text" 
                    value={this.state.username} 
                    onChange={(e) => {this.setState({username: e.target.value, error: false})}}> 
                </input>
                <br/><br/>
                <p><b>Password:  </b></p>
                <input 
                    type="password" 
                    value={this.state.password} 
                    onChange={(e) => {this.setState({password: e.target.value, error: false})}}> 
                </input>
                <br/><br/>
                <button onClick={this.authCheckHandler}>Login</button>
                {this.state.error ? <p>Wrong Password</p> : null}
                </center>
            </div>
            
        )
    }
}

export default Home;